<!-- 后台弹出层

参数：
option：
> title: 标题名
> showflagarr: 需要显示的小组件（1输入框，2权限滑动，3时间选择器） 

方法：
@closepopup： 关闭窗口绑定函数

-->

<template>
	<view style='position: absolute;width: 100%; height: 100%; background-color: rgba(0,0,0,0.3); z-index: 1000;'>
		<view class="popup-page">
			<view class="popup-title">
				<text>{{option.title}}</text>
				<view>
					<image src="../../static/img/popup/close.png" mode="scaleToFill" class="popup-title-icon" @click="popupclose"></image>
				</view>
			</view>
			<view class="popup-body">
				<view class="popup-item" v-for="(item, index) in option.showflagarr" :key="index">
					
					<view class="popup-item-text" v-if="item == 1">
						<text>{{option.keyname[index]}}</text>
						<el-input v-model="value[index]" placeholder="请输入内容" style='width: 1210rpx;'></el-input>
					</view>
					
					<view class="popup-item-button" v-if="item == 2">
						<text>{{option.keyname[index]}}</text>
						<el-switch
						  v-model="value[index]"
						  active-color="#13ce66"
						  inactive-color="#ff4949"
						  style='margin-top: 25rpx; margin-left: 100rpx;'>
						</el-switch>
					</view>
					
					<view class="popup-item-time" v-if="item == 3">
						<text>{{option.keyname[index]}}</text>
						<div class="block"
						  style='margin-left: 100rpx;'>
						    <el-date-picker
						      v-model="value[index]"
						      align="center"
						      placeholder="选择日期"
						      :picker-options="pickerOptions">
						    </el-date-picker>
						  </div>
					</view>
					
					<view class="popup-item-select" v-if="item == 4">
						<text>{{option.keyname[index]}}</text>
						<el-select v-model="value[index]" placeholder="请选择"
						  style='margin-left: 15px;'>
						    <el-option
						      v-for="item2 in selectOptions"
						      :key="item2.value"
						      :label="item2.label"
						      :value="item2.value">
						    </el-option>
						  </el-select>
					</view>
				</view>
			</view>
			
			<view class="popup-foot">
				<button type="default" style="width: 80px; height: 40px; position: absolute; right: 120px; top: 2px;" @click="ceshi">确认</button>
				<button type="default" style="width: 80px; height: 40px; position: absolute; right: 30px; top: 2px;" @click="popupclose">取消</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				input: '',
				value: [],
				showceshi: [1,2,3],
				pickerOptions: {
				  disabledDate(time) {
				    return time.getTime() > Date.now();
				  },
				  shortcuts: [{
				    text: '今天',
				    onClick(picker) {
				      picker.$emit('pick', new Date());
				    }
				  }, {
				    text: '昨天',
				    onClick(picker) {
				      const date = new Date();
				      date.setTime(date.getTime() - 3600 * 1000 * 24);
				      picker.$emit('pick', date);
				    }
				  }, {
				    text: '一周前',
				    onClick(picker) {
				      const date = new Date();
				      date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
				      picker.$emit('pick', date);
				    }
				  }]
				},
				selectOptions: [{
				    value: 'S',
				    label: 'S'
				  }, {
				    value: 'A',
				    label: 'A'
				  }, {
				    value: 'B',
				    label: 'B'
				  }, {
				    value: 'C',
				    label: 'C'
				  }, {
				    value: 'D',
				    label: 'D'
				  }],
			}
		}, 
		props: [
			'option'
		],
		methods: {
			popupclose() {
				this.$emit('closepopup')
			}, 
			ceshi() {
				console.log(this.value)
			}
		}
	}
</script>

<style lang="less" scoped>
	.popup-page {
		position: absolute;
		top: 15%;
		left: 25%;
		width: 1700rpx;
		height: 1000rpx;
		background-color: #FFFFFF;
		box-shadow: 0 0 10rpx #6c8486;
		
		// 弹窗头部
		.popup-title {
			// position: absolute;
			// top: 0;
			display: flex;
			justify-content: space-between;
			padding: 0 20rpx 0 40rpx;
			width: 1640rpx;
			height: 90rpx;
			line-height: 90rpx;
			font-size: 35rpx;
			border-bottom: 1rpx solid #dadada;
			background-color: #f0f0f0;
			
			// 标题里的icon
			.popup-title-icon {
				margin: 20rpx 10rpx;
				width: 50rpx;
				height: 50rpx;
			}
		}
		
		// 弹窗体
		.popup-body {
			padding-top: 20rpx;
			width: 1700rpx;
			height: 800rpx;
			background-color: #F1F1F1;
			
			.popup-item {
				// 输入框
				.popup-item-text {
					display: flex;
					justify-content: space-between;
					margin: 15rpx 150rpx;
					width: 1400rpx;
					height: 90rpx;
					line-height: 90rpx;
				}
				// 按钮
				.popup-item-button {
					display: flex;
					justify-content: flex-start;
					margin: 15rpx 150rpx;
					width: 1400rpx;
					height: 90rpx;
					line-height: 90rpx;
				}
				// 日期
				.popup-item-time {
					display: flex;
					justify-content: flex-start;
					margin: 15rpx 150rpx;
					width: 1400rpx;
					height: 90rpx;
					line-height: 90rpx;
				}
				// 按钮
				.popup-item-select {
					display: flex;
					justify-content: flex-start;
					margin: 15rpx 150rpx;
					width: 1400rpx;
					height: 90rpx;
					line-height: 90rpx;
				}
			}
		}
		
		// 弹窗尾
		.popup-foot {
			position: relative;
			border-top: 1rpx solid #dadada;;
			width: 1700rpx;
			height: 90rpx;
			background-color: #f0f4f7;
			display: flex;
			justify-content: flex-end;
		}
		
	}
</style>
